@import url("https://fonts.googleapis.com/css2?family=Luckiest+Guy&display=swap");

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

:root {
  --bg-color: rgb(242,244,247);
  --model-bg-color: #fff;
  --button-bg-color:#1962D1;;
}

.header-container {
  text-align: center;
  padding-top: 20px;
  font-size: 62px;
  font-family: "Luckiest Guy", cursive;
  letter-spacing: 8px;
  color:#1962D1;
}

.header-container p {
  display: inline-block;
}

.title-last-letter {
  animation: lastletter 5s;
}

@keyframes lastletter {
  0% {
    transform: rotate(1275deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

.title-tag {
  margin-left: 20px;
  letter-spacing: 3px;
}

.App {
  position: relative;
  width: 100vw;
  height: 100vh;
  background-color: var(--bg-color);
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.addtodo {
  width: 60px;
  height: 60px;
  cursor: pointer;
  position: absolute;
  bottom: 25px;
  right: 25px;
  z-index: 9990;
  transition: transform 100ms;
}

.addtodo:active {
  transform: scale(1.2);
}

#model-backdrop {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100vw;
  height: 100vh;
  background: #00000099;
  z-index: 9991;
  display: grid;
  place-items: center;
}

#model-screen {
  background: var(--model-bg-color);
  position: relative;
  padding: 10px;
  border-radius: 5px;
  min-width: 350px;
  max-width: 90vw;
  min-height: 350px;
  max-height: 90vh;
  overflow-x: hidden;
  overflow-y: auto;
  animation: model-screen-slide-up 200ms ease-in;
}

@keyframes model-screen-slide-up {
  from {
    opacity: 0;
    transform: translateY(100vh);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

#close-menu {
  position: absolute;
  right: 10px;
  cursor: pointer;
}
.todosetup-container {
  text-align: center;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

.todosetup-container h2 {
  
  font-size: 38px;
  margin-top: 30px;
  color: var(--button-bg-color);
}
.todosetup-container input {
  display: block;
  margin: auto;
  margin-top: 20px;
  padding: 10px 50px;
  padding-left: 7px;
  padding-bottom: 5px;
  border: 0;
  border-bottom: 1px solid rgb(158, 159, 171);
  outline: none;
  font-size: 16px;
}
.todosetup-container input:focus{
  border-bottom: 2.2px solid var(--button-bg-color);
  background-color: var(--bg-color);
}
.todosetup-container textarea{
  display: block;
  margin: auto;
  resize: none;
  border: 0;
  margin-top: 22px;
  outline: none;
  border-bottom: 1px solid rgb(158, 159, 171);
  width: 250px;
  height: 90px;
  padding: 5px;
  font-size: 16px;
  font-family: inherit;
}
.todosetup-container textarea:focus{
  border-bottom: 2.2px solid var(--button-bg-color);
  background-color: var(--bg-color);
}

.todosetup-container button{
  margin-top: 25px;
  font-size: larger;
  border: 0;
  background-color: var(--button-bg-color);
  padding: 10px 40px;
  border-radius: 25px;
  color: white;
  cursor: pointer;
}


/* https://linuxpip.org/vscode-format-on-save/ */

/* -------- TodoCard --------- */
#todo-container{
  display: grid;
  grid-template-columns: repeat(auto-fill,minmax(310px,1fr));
  justify-items: center;
  align-items: start;
  width: 1000px;
  max-width: 90vw;
  max-height: 100%;
  margin: 20px auto 0;
  padding: 0 10px 20px;
  overflow-y: auto;
  font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}
#todo-container svg{
  fill:  rgb(94,97,117);
  cursor: pointer;
}

#todo-container svg:active{
  transform: scale(1.2);
}
#todo-container::-webkit-scrollbar{
width: 6px;
background: #5e617525;
}#todo-container::-webkit-scrollbar-thumb{
  background-color: #a8a8ac;
}

.todo-card{
  padding: 5px;
  margin: 10px;
  /* border: solid .5px #000; */
  width: 300px;
  height: 250px;
  border-radius: 10px;
  position: relative;
  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
  display: flex;
  flex-direction: column;
  background-color: white;
}

.todo-card > h3{
  border-bottom: solid .5px rgba(0, 0, 0, 0.22);
  padding-bottom: 5px;
  margin: 0 0 5px;
  color: rgb(193,45,124);
  text-align: center;
}

.todo-card > p{
  height: 100%;
  overflow: hidden;
  color: rgb(94,97,117);
}
.todo-bottom{
  display: flex;
  justify-content: space-between;
  align-items:flex-end;
  padding-bottom: 2px;
  font-size: 13px;
  color:rgb(158,159,171)
}
.todo-del{
  margin-left: 10px;
}

